	module.exports = {"data":{"wordpressPost":{"title":"How to get the best lashes of your life","content":"<p>My eyelashes are long and they curl up in a super pretty fashion which is awesome. They’re blonde however which is not awesome at all. Why? Because despite their length and sweet curve they’re pretty much invisible.</p>\n<p>To combat that problem (first world yes, but a problem nonetheless) I fall back on three different options. Here, a quick run-down of that snappy trio so that you can choose the one that’s right for you.</p>\n<p><strong>Mascara</strong></p>\n<p>Mascara is straight-up genius. It was invented back in the late 1800s when women were whipping up their own versions often using a blend of coal dust and petroleum jelly.</p>\n<p>In the early 1900s, the first commercial mascaras hit the market and the product has been selling like hot cakes ever since. Today’s versions can add length, provide curl, rich colour and even growth-boosting nutrients. The most dramatic colour will always be the blackest of blacks.</p>\n<p>Tip: Wiggle the wand from base to tip to avoid clumping and do both the underneath and the top of the upper lashes.</p>\n<p><strong>Lash Extensions</strong></p>\n<p>These are individual lashes which are glued onto your own by a specialised lash therapist. They come in both synthetic and natural varieties. You can opt for very long, more curled or even have them attached to the outer eye only for a sexy doe-eyed look.</p>\n<p>Before you have them applied your therapist will analyse the shape of your eye and advise you as to which will be most flattering. Lash extensions are terrific in that they last for roughly six weeks so you look beautiful from the moment you well, open your eyes.</p>\n<p>Tip: Extensions look their best when more natural &#8211; we suggest you skip the showgirl versions.</p>\n<p><strong>False Lashes</strong></p>\n<p>These guys used to be super difficult to apply but falsies have come a long way in recent years. Full lash and half lash versions are available as are singles. Chemcorp Beauty Expert Rachael Brook says the version you choose is entirely up to you and also, your eye shape, but she prefers single lashes because they look the most realistic.</p>\n<p>While false lash application can be difficult to master, a few practise runs and you will get the routine down pat. Promise.</p>\n<p>Tip: Use a tiny amount of glue on the tip of the individual lash or along the entire band if it’s a full set and wait 30 seconds for the glue to become tacky before you apply, says Rachael. Lay the falsies over your natural lashes &#8211; not your eyelid, she adds.</p>\n<p><strong>We Love&#8230;</strong></p>\n<p>Maybelline New York Colossal Volum’Express Mascara, $19.95. 1300 369 327.</p>\n<p>Rimmel London 24HR SuperCurler Mascara, $17.95. 1800 812 663.</p>\n<p>1000 Hour Black Flared Short Individual Re-Usable Lashes, $9.50. 1000hour.com.au</p>\n<p>For lash extensions hop onto a search engine such as google and search for a salon in your area. Extensions cost from $80 to $250 depending on the style, number you have applied and also the individual salon.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/07/28164816/beauty_leiane_tuesday3630-1.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"ddf610ef-be78-54dd-a032-b3855521a681"}}

